/* @mixin button($text, $background-color, $outline: true, $padding, $color-text-hover) {
  @include font-par(font-family(opensans), font-weight(bold), font-size(base-t), normal, color($base-c, white), font-lh(base));
  @include box-shadow(0, 0, 0, color($main-c, white), true, 2px);
  background-color: transparent;

  &:hover {
    background-color: color($main-c, secondary);
  }

  &:active {
    color: rgba(color($base-c, white), 0.3);
  }
}
*/
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

svg path,
rect,
g {
  fill: inherit;
  stroke: inherit;
}

@font-face {
  font-family: "Open Sans";
  src: url("/fontsopensans.eot");
  src: url("/fontsopensans.eot#iefix") format("embedded-opentype"), url("/fontsopensans.woff2") format("woff2"), url("/fontsopensans.woff") format("woff"), url("/fontsopensans.ttf") format("truetype"), url("/fontsopensans.otf") format("opentype"), url("/fontsopensans.svg") format("svg");
  font-style: normal;
  font-weight: normal;
}

