.button {
  box-shadow: inset 0 0 0 2px #444444;
  font-family: "Open Sans", Arial, sans-serif;
  font-size: 17px;
  font-weight: 700;
  font-style: normal;
  line-height: 20px;
  color: #444444;
  background-color: transparent;
  text-transform: uppercase;
  padding: 10px 85px;
  width: 100%;
  display: block;
  text-align: center;
  margin: 0;
}

.button__inner {
  width: 100%;
  padding: 25px 0 15px 30px;
  margin: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

@media (min-width: 768px) {
  .button__inner {
    padding: 25px 0 15px 36px;
  }
}

@media (min-width: 768px) {
  .button__inner {
    padding: 25px 0 15px 37px;
  }
}

.button--primary {
  width: 100%;
  background-color: #63d1bb;
  font-family: "Open Sans", Arial, sans-serif;
  font-size: 19px;
  font-weight: 700;
  font-style: normal;
  line-height: 24px;
  color: #ffffff;
  text-transform: none;
  border: 0;
  box-shadow: none;
  text-align: left;
  min-height: 103px;
}

@media (min-width: 768px) {
  .button--primary {
    font-size: 20px;
    line-height: 30px;
  }
}

.button--icon--interior:hover {
  position: relative;
  background-color: #59d5bc;
}

.button--icon--interior:hover .button__inner--icon--interior {
  position: absolute;
  left: 0;
  bottom: 5px;
  background-color: #59d5bc;
}

.button--icon--interior:hover .button__svg--interior {
  fill: rgba(20, 144, 128, 0.7);
}

.button--icon--interior:active {
  background-color: #59d5bc;
  color: rgba(255, 255, 255, 0.3);
}

.button--icon--interior:active .button__svg--interior {
  fill: rgba(20, 144, 128, 0.3);
}

.button--icon--toy {
  background-color: #56c3b2;
}

.button--icon--toy:hover {
  position: relative;
  background-color: #46c1ae;
}

.button--icon--toy:hover .button__inner--icon--toy {
  position: absolute;
  left: 0;
  bottom: 5px;
  background-color: #46c1ae;
}

.button--icon--toy:hover .button__svg--toy {
  fill: rgba(20, 144, 128, 0.7);
}

.button--icon--toy:active {
  background-color: #46c1ae;
  color: rgba(255, 255, 255, 0.3);
}

.button--icon--toy:active .button__svg--interior {
  fill: rgba(20, 144, 128, 0.3);
}

.button__text {
  white-space: pre-line;
}

.button__svg--interior {
  margin-right: 36px;
  fill: rgba(20, 144, 128, 0.5);
}

.button__svg--toy {
  margin-right: 27px;
  fill: rgba(20, 144, 128, 0.5);
}

@media (min-width: 768px) {
  .button__svg--toy {
    margin-right: 33px;
  }
}

@media (min-width: 1150px) {
  .button__svg--toy {
    margin-right: 35px;
  }
}

